form{
	ul{
		margin:0;
		padding:0;
		list-style:none;
	}
	li{
		margin-bottom:20px;
	}
}

#{$all-text-inputs} {
	appearance:none;
	width:100%;
	appearance:none;
	background:$color-gray-med;
	color:$color-white;
	font-size:0.9em;
	padding:1em;
	border:none;
	border-radius:0;
	font-family:$primary;
	@include placeholder{
		color:$color-white;
		letter-spacing:3px;
		text-transform:uppercase;
		font-weight:100;
	}
}

select{
	appearance:none;
	display:block;
	border-radius:0;
	background:$color-gray-med;
	color:$color-white;
	padding:1em;
	width:100%;
	font-size:0.9em;
	border:0;
	text-transform:uppercase;
	border:none;
	font-family:$primary;
	letter-spacing:1px;
}

textarea{
	margin:25px 0;
}

input[type="submit"]{
	appearance:none;
	font-family:$primary;
	padding:1em 6em;
	border:none;
	border-radius:none;
	background:$color-black;
	color:$color-white;
	font-size:0.9em;
	text-align:left;
	text-transform:uppercase;
	letter-spacing:3px;
	font-weight:100;
	transition:.3s;
	border-radius:0;
	&:hover{
		transform:translateX(-5px) translateY(-5px);
		box-shadow: 5px 5px 0px $color-blue;
	}
	@include media($mobile){
		width: 100%;
		text-align: center;
	}
}

.ginput_container_select{
	position:relative;
	&:after{
		content:'';
		display:block;
		border-left:2px solid $color-white;
		border-bottom:2px solid $color-white;
		@include size(15px);
		@include position(absolute, 40% 25px null null);
		@include transform(translateY(-50%) rotate(-45deg));
	}
}

.gfield_required, .charleft{
	display:none;
}

.gfield_description, .validation_error{
	color:#E72D27 !important;
}

//Marketing landing page form styles
.landing-page{
	input[type="text"]{
		background-color:$color-white;
		color:#7d7d7d;
		@include placeholder{
			color:#7d7d7d;
			letter-spacing: 1px;
			font-size:1.25rem;
			@include media($mobile){
				font-size:.9rem;
			}
		}
	}
	.gfield_select{
		background-color: $color-white;
		color:#7d7d7d;
		padding:12px;
		font-size:1.2rem;
	}
	input[type="submit"]{
		padding:1.25rem 5rem;
		letter-spacing:1px;
		font-size:1.1em;
		font-weight:600;
		background-color: #ED3529;
		cursor:pointer;
		transition:.5s;
		&:hover{
			box-shadow:none;
			transform:none;
			@include media($desktop){
				background-color: #3A3A3A;
			}
		}
	}
}